.block--l .table-block {
  overflow: visible !important;
}
.block--l .table-block.content-block {
  padding-left: 0 !important;
  padding-right: 0 !important;
}
@media screen and (min-width: 1025px) {
  .block--l .table-block table tbody tr td:first-of-type {
    font-size: 24px;
    line-height: 36px;
    font-weight: 600;
  }
}
@media screen and (max-width: 1024px) {
  .block--l .table-block table tbody tr td:first-of-type {
    font-size: 22px;
    line-height: 30px;
    font-weight: 600;
  }
}